"use strict";
// Convert from '.twemoji>svg' to embedded img
var twemojiConverter = function () {
    var entries = document.querySelectorAll('.twemoji>svg');
    if (!entries || entries.length == 0)
        return;
    entries.forEach(function (svgElement) {
        var imgElement = document.createElement('img');
        var styles = window.getComputedStyle(svgElement);
        var fillColor = styles.color || '#333';
        svgElement.setAttribute('style', "fill:" + fillColor + ";");
        //imgElement.setAttribute('style', `width:${styles.width};height:${styles.height};`)
        //imgElement.setAttribute('style', `width:1.125rem;height:auto;`)
        var serializedSVG = new XMLSerializer().serializeToString(svgElement);
        var src = 'data:image/svg+xml;base64,' + window.btoa(serializedSVG);
        imgElement.setAttribute('src', src);
        svgElement.parentNode.replaceChild(imgElement, svgElement);
    });
};
twemojiConverter();
